        </div>
      </div>
    </li>
    <div class="footer">
      <div class="wrap">
        <span>收藏歌单</span>
      </div>
    </div>
  </ul>
</template>

<script>
export default {
  props: ["comments"],
  data: function () {
    return {
      date: new Date(),
    };
  },
  methods: {
    unixToDate: function (value) {
      if (!value) return "";
      var d = new Date(value);
      return (
        d.getFullYear() + "年" + (d.getMonth() + 1) + "月" + d.getDate() + "日"
      );
    },
  }
};
</script>

<style lang="less" scoped>
.comment-list {
  .comment {
    height: 23px;
    font-weight: normal;
    line-height: 23px;
    padding: 0 10px;
    font-size: 12px;
    color: #666;
    background-color: #eeeff0;
  }
  li {
    margin-top: 10px;
    display: flex;
    
    .left {
      height: 35px;
      margin: 0 10px;

      img {
        widows: 30px;
        height: 30px;
        display: block;
        border-radius: 50%;
      }
    }
    .right {
      flex: 1;
      border-bottom: 1px solid #e1e1e1;
      padding-right: 10px;
      padding-bottom: 11px;
      display: flex;

      .con {
        flex: 1;
      }

      .time {
        font-size: 9px;
        color: #999;
      }

      .content {
        color: #333;
        font-size: 15px;
        line-height: 22px;
        margin-top: 5px;
      }
      .give-like {
        width: 40px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        .like {
          color: #999;
          font-size: 12px;
          line-height: 22px;
        }
      }
      .nickname {
        color: #666;
        font-size: 14px;
        line-height: 20px;
      }
    }
  }
  &:last-child{
        margin-bottom:100px;
    }
  .footer {
    width: 100%;
    background: #fff;
    position: fixed;
    left: 0;
    bottom: 0;
    right: 0;
    margin-top:20px;
    .wrap {
      padding-left: 30px;
      padding-right: 30px;
      padding-top: 7px;
      padding-bottom: 7px;

      span {
        display: block;
        height: 40px;
        line-height: 40px;
        text-align: center;
        color: #fff;
        font-size: 16px;
        background: #ff3a3a;
        border-radius: 36px;

        &::before {
          content: "";
          display: inline-block;
          position: relative;
          top: 5px;
          width: 21px;
          height: 23px;
          background: url(data:image/svg+xml;base64,)
            no-repeat;
          background-size: contain;
          margin-right: 4px;
        }
      }
    }
  }
}
</style>