<template>
  <div class="rank-songs" v-if="details">
    <YunMusicHeader :details="details" />
    <div class="interac-data">
      <span class="iconfont icon-tianjia">{{ details.subscribedCount }}</span>
      <span class="iconfont icon-pinglun">{{ details.commentCount }}</span>
      <span class="iconfont icon-fenxiang">{{ details.shareCount }}</span>
    </div>
    <ul class="songs-lists">
      <LatestMusic
        :song="song"
        v-for="(song, index) in details.tracks.slice(0, 20)"
        :key="index"
        :order="index + 1"
        @update:current-play-music="$emit('update:current-play-music', $event)"
        :currentPlayMusicId="currentPlayMusicId"
        :playing="playing"
        :currentPlayMusic="currentPlayMusic"
        @click.native="$emit('update:playlist', details.tracks.slice(0, 20))"
      />
    </ul>
    <PlayMask> </PlayMask>
  </div>
</template>

<script>
import YunMusicHeader from "@/components/YunMusicHeader.vue";
import LatestMusic from "@/components/LatestMusic.vue";
import PlayMask from "@/components/PlayMask.vue";
export default {
  components: {
    YunMusicHeader,
    LatestMusic,
    PlayMask,
  },
  props: ["currentPlayMusicId", "playing", "currentPlayMusic"],
  data: function () {
    return {
      details: null,
      comments: null,
    };
  },
  created: function () {
    this.getTracksById(this.$route.query.id);

    this.axios
      .get(
        "https://apis.netstart.cn/music/comment/hot?id=" + this.$route.query.id
      )
      .then((res) => {
        console.log(res);
      });
    this.axios
      .get(
        "https://apis.netstart.cn/music/comment/playlist?id=" +
          this.$route.query.id +
          "&type=2"
      )
      .then((res) => {
        console.log(res);
        this.comments = res.data;
      });
  },
  methods: {
    getTracksById: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/playlist/detail?id=" + id)
        .then((res) => {
          this.details = res.data.playlist;
          // console.log(this.details);
        });
    },
  },
};
</script>

<style lang="less" scoped>
@import "../css/iconfont.css";
.rank-songs {
  .interac-data {
    display: flex;
    width: 300px;
    height: 60px;
    background: #f5f5f5;
    border-radius: 30px;
    position: absolute;
    right: 35px;
    top: 187px;
    text-align: center;
    box-shadow: 0px 2px 6px 0px #ccc;

    .iconfont {
      font-size: 16px;
      padding: 0px 5px;
      text-align: center;
      color: #000;
    }

    // .icon-pinglun::before {
    //   color: #ccc;
    //   content: "|";
    // }
    .icon-tianjia::after {
      content: "|";
      color: #ccc;
      padding-left: 10px;
    }

    .icon-pinglun::after {
      content: "|";
      color: #ccc;
      padding-left: 10px;
    }

    span {
      flex: 1;
      text-align: center;
      line-height: 60px;
    }
  }

  .songs-lists{
    margin-top:40px;
  }
  .title {
    height: 23px;
    font-weight: normal;
    line-height: 23px;
    padding: 0 10px;
    font-size: 12px;
    color: #666;
    background-color: #eeeff0;
  }

  .more {
    margin-top: 13px;
    color: #999;
    font-size: 14px;
    text-align: center;
  }
}
</style>