// 头像做动画
@keyframes myRotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.playbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  // padding: 0 10px;
  background: #fff;
  width: 100%;
  height: 50px;
  position: fixed;
  left: 0;
  bottom: 0;
