        width: 33px;
        height: 33px;
        border-radius: 50%;
        position: absolute;
        left: 50%;
        top: 50%;
        vertical-align: middle;
        margin-left: -16px;
        margin-top: -17px;
        animation: myRotate 10s linear infinite;
        animation-play-state: paused;

        &.playing {
          animation-play-state: running;
        }
      }
    }

    .div {
      width: 70%;
      font-size: 13px;
      margin-left: 5px;
      display: -webkit-box;
      -webkit-line-clamp: 1;
