<template>
  <div class="rank-songs" v-if="details">
    <RankSongsHeader :details="details"/>
    <ul class="songs-lists">
        <LatestMusic :song="song" v-for="(song,index) in details.tracks" :key="index" :order="index+1" @currentPlayMusic="$emit('currentPlayMusic'),$event"/>
    </ul>
  </div>
</template>

<script>
import RankSongsHeader from "@/components/RankSongsHeader.vue";
import LatestMusic from "@/components/LatestMusic.vue";
export default {
  components: {
    RankSongsHeader,
    LatestMusic
  },
  props:["playing","currentPlayMusicId"],
  data: function () {
    return {
        details:null,
        records:null
    };
  },
  created: function () {
    this.getTracksById(this.$route.query.id);
  },
  methods: {
    getTracksById: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/playlist/detail?id=" + id)
        .then((res) => {
          this.details = res.data.playlist;
          console.log(this.details);

        });
    },
  },
};
</script>

<style lang="less" scoped>
.songs-lists{
    margin-top: 120px;
}
</style>