<template>
  <li class="rank-card" v-if="item">
    <div class="left">
      <span
        class="iconfont icon-play"
        style="color: rgba(255, 255, 255, 0.7); font-size: 25px"
      ></span>
      <h3 class="title">{{ item.name }}</h3>
      <div class="thumb" v-if="playlists">
        <img
          v-for="song in playlists.tracks.slice(0, 3)"
          :key="song.id"
          :src="song.al.picUrl + '?imageView=1&type=webp&thumbnail=247x0'"
          alt=""
        />
      </div>
    </div>
    <div class="right">
      <span>{{ item.updateFrequency }}</span>
      <ul v-if="playlists" class="lis">
        <li
          v-for="(song, index) in playlists.tracks.slice(0, 3)"
          :key="song.id"
        >
          {{ index + 1 }}.{{ song.name }}<i> - </i><b>{{ song.ar[0].name }}</b>
        </li>
      </ul>
    </div>
  </li>
</template>

<script>
export default {
  props: ["item", "currentPlayMusic", "currentPlayMusicId"],
  data: function () {
    return {
      playlists: null,
    };
  },
  created: function () {
    this.axios
      .get("https://apis.netstart.cn/music/playlist/detail?id=" + this.item.id)
      .then((res) => {
        setTimeout(() => {
          this.playlists = res.data.playlist;
          // console.log(this.playlists.tracks.slice(0,20));
        }, 3000);
      });
  },
};
</script>

<style lang="less" scoped>
// 引入css图标文件
@import "../css/iconfont.css";
.song-list {
  flex-grow: 1;
  flex-basis: calc(100vw / 3 - 10px);

  .rank-card {
    list-style: none;
    background: pink;
    margin: 10px 15px;
    padding: 5px 15px 10px 0;
    border-radius: 10px;
    display: flex;
    height: calc(100vw / 3 - 10px);
    background: url(../assets/loading1.jpg) no-repeat;
    background-size: 30px 30px;
    background-position: center;

    &:nth-child(1) {
      background: #f3e0e6;

      .title {
        color: #e72d76;
      }
    }

    &:nth-child(2) {
      background: #e0eae9;

      .title {
        color: #279689;
      }
    }

    &:nth-child(3) {
      background: #e2e6ef;

      .title {
        color: #3d70bd;
      }
    }

    &:nth-child(4) {
      background: #f3e1e1;

      .title {
        color: #e22424;
      }
    }

    .left {
      width: 100px;
      position: relative;
      height: calc(100vw / 3 - 10px);
      // background: url(../assets/loading1.jpg) no-repeat;
      background-image:-webkit-linear-gradient(top,rgba(0,0,0,.2),rgba(0,0,0,0));background-image:linear-gradient(180deg,rgba(0,0,0,.2),rgba(0,0,0,0));
      background-repeat: no-repeat;
      background-size: 50px 50px;
      background-position: center;

      .icon-play {
        z-index: 4;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%);
      }

      h3 {
        text-align: center;
        line-height: 30px;
        padding: 2px 5px;
      }

      .thumb {
        position: relative;
        text-align: center;
      }

      img {
        width: 100px;
        position: absolute;
        right: 0;
        bottom: 0;
        vertical-align: middle;
        border-radius: 10px;

        &:first-child {
          position: relative;
          width: 60px;
          z-index: 3;
        }

        &:nth-child(2) {
          width: 50px;
          right: 9px;
          z-index: 2;
          opacity: 0.6;
        }

        &:nth-child(3) {
          width: 40px;
          z-index: 1;
          opacity: 0.4;
        }
      }
    }

    .right {
      list-style: none;
      margin-left: 30px;
      position: relative;
      flex: 1;

      span {
        color: #888;
        font-size: 10px;
        position: absolute;
        right: -5px;
        top: 0;
      }

      .lis {
        list-style: none;
        margin-top: 10px;

        li {
          line-height: 30px;
          font-size: 16px;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          overflow: hidden;

          b {
            font-weight: normal;
            font-size: 16px;
            color: #888;
          }
        }
      }
    }
  }
}
</style>