<template>
  <li
    class="latest-music"
    :class="{ current: song.id === currentPlayMusicId, playing: playing }"
    @click="$emit('update:current-play-music', song)"
    :playing="playing"
  >
    <span class="order" v-if="order">{{ order }}</span>
    <div class="music_lists">
      <div class="info-box">
        <h5 class="song_name">{{ song.name }}</h5>
        <span v-if="song.song" class="des"> {{ song.song.alias[0] }}</span>
      </div>
      <div class="signers">
        <span class="quality"></span>
        <span
          class="signer"
          v-for="(item, index) in song.song ? song.song.artists : song.ar"
          :key="index"
          >{{ item.name }}</span
        >
        <span class="monicker">{{
          song.song ? song.song.album.name : song.al.name
        }}</span>
      </div>
    </div>
    <span class="play_btn">
      <div class="animate">
        <span></span>
        <span></span>
        <span></span>
      </div>
    </span>
  </li>
</template>

<script>
export default {
  props: {
    song: Object,
    order: Number,
    currentPlayMusicId: [String, Number],
    playing: Boolean,
    // currentPlayMusic:Object
  },
  methods: {
    currentPlayMusic: function (id) {
      this.$router.push({
        path: "/playlist",
        query: {
          id,
        },
      });
    },
  },
};
</script>

<style lang="less" scoped>
// play_btn做动画
@keyframes loading {
  from {
    transform: scaleY(0.2);
  }
  to {
    transform: scaleY(1);
  }
}
.latest-music {
  display: flex;
  align-items: center;
  border: 1px solid #f6f6f7;

  .order {
    width: 20px;
    font-size: 17px;
    display: flex;
    align-items: center;
    padding: 5px;
    color: #ccc;
  }
  &:nth-child(-n + 3) .order {
    color: red;
  }

  .music_lists {
    flex: 1;
    margin-left: 10px;
    padding: 6px 0;

    .info-box {
      display: flex;
      .song_name {
        font-size: 17px;
        font-weight: normal;
        // display: -webkit-box;
        // -webkit-line-clamp: 1;
        // -webkit-box-orient: vertical;
        // overflow: hidden;
        width: 200px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }
      .des {
        color: #888;
        margin-left: 4px;
        width: 100px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }
    }

    .signers {
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      .quality {
        display: inline-block;
        background: url(../image/index_icon_2x.png) no-repeat 0 0;
        background-size: 166px 97px;
        width: 12px;
        height: 8px;
        margin-right: 4px;
      }

      .signer {
        font-size: 12px;
        color: #888;

        &::after {
          content: "-";
          padding: 0 5px;
        }

        &:last-of-type::after {
          content: "-";
        }
      }

      .monicker {
        font-weight: normal;
        font-size: 12px;
        color: #888;
      }
    }
  }
  .play_btn {
    display: inline-block;
    background: url(../image/index_icon_2x.png) no-repeat -24px 0;
    background-size: 166px 97px;
    width: 22px;
    height: 22px;
    margin-right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &.current {
    .play_btn {
      display: block;
      width: 22px;
      height: 22px;
      border-radius: 50%;
      // background: url(../assets/loading.gif);
      background-size: 100% 100%;
      // background: red;
      background: none;
      display: flex;
      justify-content: center;
      align-items: center;

      .animate {
        display: flex;
        height: 80%;
        width: 80%;
        justify-content: space-between;
        align-items: center;
        span {
          width: 20%;
          height: 100%;
          background: #d43c33;
          // 动画
          animation: loading 1s linear infinite alternate;
          animation-play-state: paused;
          // 改变源点
          transform-origin: bottom;

          &:nth-child(1) {
            animation-delay: -0.6s;
          }
          &:nth-child(2) {
            animation-delay: -0.3s;
          }
        }
      }
    }
  }

  &.playing {
    .play_btn {
      .animate {
        span {
          animation-play-state: running;
        }
      }
    }
  }
}
</style>