<template>
  <div class="song-detail">
    <section class="match-lists">
      <!-- <h4>最佳匹配</h4> -->
      <ul class="list">
        <h4>最佳匹配</h4>
        <li class="info" v-if="singers" @click="showHomePage">
          <div class="left" v-for="item in singers.artist" :key="item.id">
            <img :src="item.picUrl | imageView(50)" alt="" />
          </div>
          <div
            class="special"
            v-for="item in singers.artist"
            :key="item.img1v1Id"
          >
            <h4 class="des">
              {{ item.occupation }}:
              <p>
                <span class="highlight">{{ item.name }}</span>
                <span class="normal" v-if="item.alias[0]"
                  >({{ item.alias[0] }})</span
                >
              </p>
            </h4>
          </div>
          <i class="gt" v-if="show"></i>
        </li>
        <li
          v-for="item in songlists"
          :key="item.id"
          @click="
            $emit('update:current-play-music', {
              id: item.id,
              name: item.name,
              picUrl: item.artists[0].img1v1Url,
            })
          "
          :class="{ current: item.id === currentPlayMusicId, playing: playing }"
        >
          <div class="left">
            <div class="top">{{ item.name }}</div>
            <div class="bottom">
              <span
                v-for="(item, index) in item.artists ? item.artists : item.ar"
                :key="index"
                class="highlight"
                >{{ item.name }}</span
              >
              <span class="album">{{ item.album.name }}</span>
            </div>
          </div>
          <div class="right">
            <span></span>
          </div>
        </li>
      </ul>
    </section>
    <transition name="fade">
      <Play
        v-if="isFullScreen"
        :playing="playing"
        :currentPlayMusic="currentPlayMusic"
      ></Play>
    </transition>

    <transition name="fade">
      <HomePage
        v-if="show"
        :introduce="introduce"
        :details="details"
      ></HomePage>
    </transition>
  </div>
</template>

<script>
import Play from "@/components/Play.vue";
import HomePage from "@/components/HomePage.vue";
export default {
  props: [
    "playing",
    "songlists",
    "currentPlayMusic",
    "currentPlayMusicId",
    "singers",
  ],
  components: { Play, HomePage },
  data: function () {
    return {
      details: null,
      isFullScreen: false,
      show: false,
      introduce: null,
    };
  },
  created: function () {
    this.axios
      .get("https://apis.netstart.cn/music/search?keywords=" + this.value)
      .then((res) => {
        this.details = res.data.result.songs;
        // console.log(this.details);
      });

    // 多重匹配
    // this.axios
    //   .get(
    //     "https://apis.netstart.cn/music/search/multimatch?keywords=" +
    //       this.value
    //   )
    //   .then((res) => {
    //     this.singers = res.data.result;
    //     console.log(this.singers);
    //   });
  },
  methods: {
    clickHandler: function () {
      this.isFullScreen = true;
    },
    showHomePage: function () {
      this.show = true;
      this.axios
        .get(
          "https://apis.netstart.cn/music/artist/detail?id=" +
            this.singers.artist[0].id
        )
        .then((res) => {
          console.log(res);
          this.introduce = res.data.data;
          // console.log(this.introduce);
        });
    },
  },
};
</script>

