<template>
  <div class="home-page">
    <div class="top" v-if="introduce">
      <div
        class="coverImg"
        :style="{ backgroundImage: 'url(' + introduce.artist.cover + ')' }"
      >
      <div class="xinxi">
        <h3 class="stage-name">{{ introduce.artist.name }}</h3>
        <span class="nickname">昵称:{{ introduce.artist.name }}</span>
        <p>
          <span>{{ introduce.artist.identifyTag }}</span>
          职业:
          <span
            v-for="item in introduce.secondaryExpertIdentiy"
            :key="item.expertIdentiyId"
            class="work">{{ item.expertIdentiyName }}</span
          >
        </p>
        </div>
      </div>
      <section class="basic-info">
        <h3 class="singer">歌手简介</h3>
        <p class="des">{{ introduce.artist.briefDesc }}</p>
        <div class="more">
        <a href="#">完整歌手介绍...</a>
        </div>
      </section>

      <!-- <section class="hot">
        <h3>热门单曲</h3>
      </section> -->
    </div>

    <section>
    </section>
  </div>
</template>

<script>
export default {
  props: ["introduce","details"],
  data:function(){
    return {
      isshowAll:true,
      isRoll:true,
      briefDesc:null
    }
  },
  watch:{
    "introduce.artist.briefDesc":function(){
        if(this.introduce.artist.briefDesc.length > 200){
            this.briefDesc = this.introduce.artist.briefDesc.substr(0,200);
            this.isshowAll = false
        }else{
          this.isshowAll = true
        }
    }
  },
  methods:{
    toDown:function(){
      this.briefDesc = this.introduce.artist.briefDesc;
      this.isRoll = false;
    }
  }
};
</script>

<style lang="less" scoped>
.home-page {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100vw;
  height: 100vh;
  background: #fff;

  .top {
    width: 100vw;
    height: 50vh;
    .coverImg {
      width: 100%;
      height: 100%;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      color: #fff;
      position: relative;

      .stage-name {
        color: #fff;
        font-weight: normal;
      }
    }
    .xinxi{
      padding:10px;
      position: absolute;
      left:0;
      bottom:10px;

      .nickname{
        display:block;
        margin-top:5px;
        margin-bottom:5px;
      }

      .work{
        padding: 0 2px;
      }
    }
    .basic-info {
      .singer{
        padding:10px;
      }
        .des {
           display: -webkit-box;
          -webkit-line-clamp: 4;
          -webkit-box-orient: vertical;
          overflow: hidden;
          color:#333;
          // margin:10px 5px;
          padding:10px 10px;
        }
        .more{
          display:flex;
          justify-content: center;
          align-items: center;
          a{
            display: inline-block;
            text-align: center;
            text-decoration: none;
            color:#333;
  
      }
    }
  }
}
}
</style>