<template>
  <li class="recommend-list">
    <div class="thumb">
      <img
        :src="item.picUrl + '?imageView=1&type=webp&thumbnail=247x0'"
        alt=""
      />
      <span class="count">{{ playCount }}</span>
    </div>
    <p>{{ item.name }}</p>
  </li>
</template>

<script>
export default {
  components: {},
  props: {
    item: Object,
    currentPlayMusic: Array,
    currentPlayMusicId: [Number, String],
  },
  computed: {
    playCount: function () {
      return this.item.playCount > 100000
        ? (this.item.playCount / 10000).toFixed(2) + "万"
        : this.item.playCount;
    },
  },
};
</script>

