<template>
  <div class="rank-songs-header">
    <div class="box">
    <span class="title">{{details.name}}</span>
    </div>
    <div
      class="background"
      :style="{
        backgroundImage:
          'url(' +
          details.coverImgUrl +
          '?imageView=1&type=webp&thumbnail=247x0)',
      }"
    >
      <span class="iconfont icon-fanhui" @click="clickHandler"></span>
      <!-- <img
        :src="details.coverImgUrl + '?imageView=1&type=webp&thumbnail=247x0'"
        alt=""
      /> -->
      <span class="time">{{ updateDate(date) }}</span>
      <i class="iconfont icon-gantanhaozhong"></i>
      <div class="interac-data">
        <span class="iconfont icon-tianjia">{{ details.subscribedCount }}</span>
        <span class="iconfont icon-pinglun">{{ details.commentCount }}</span>
        <span class="iconfont icon-fenxiang">{{ details.shareCount }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["details"],
  data: function () {
    return {
      date: new Date(),
    };
  },
  methods: {
    clickHandler: function () {
      // router.go(-1) 后退一步记录，等同于 history.back()
      this.$router.go(-1);
    },
    updateDate: function (value) {
      if (!value) return "";
      var d = new Date(value);
      return d.getMonth() + 1 + "月" + d.getDate() + "日更新";
    },
  },
};
</script>

<style lang="less" scoped>
@import "../css/iconfont.css";
.rank-songs-header {
  display: flex;
  position: relative;
  height: 250px;

  .title{
      z-index:11;
      color:#fff;
      font-size: 18px;
      // position: fixed;
      // left:36px;
      // top:7px;
  }

  .background {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: brightness(1);
    z-index: 10;

    .icon-fanhui {
      position: fixed;
      left: 10px;
      top: 10px;
      font-size: 20px;
      color: #fff;
    }

    img {
      width: 30%;
      background-size: cover;
      position: relative;
    }

    .time {
      font-size: 14px;
      color: #f5f5f5;
      position: absolute;
      left: 38%;
      bottom: 60px;
    }

    .icon-gantanhaozhong {
      position: absolute;
      right: 33%;
      bottom: 59px;
      font-size: 20px;
      color: #f5f5f5;
    }
  }

  .interac-data {
    display: flex;
    width: 300px;
    height: 60px;
    background: #f5f5f5;
    border-radius: 30px;
    position: absolute;
    right: 40px;
    bottom: -30px;
    text-align: center;
    box-shadow: 0px 2px 6px 0px #ccc;

    .iconfont {
      font-size: 16px;
      padding: 0px 5px;
      text-align: center;
      color: #000;
    }

    // .icon-pinglun::before {
    //   color: #ccc;
    //   content: "|";
    // }
    .icon-tianjia::after {
      content: "|";
      color: #ccc;
      padding-left: 10px;
    }

    .icon-pinglun::after {
      content: "|";
      color: #ccc;
      padding-left: 10px;
    }

    span {
      flex: 1;
      text-align: center;
      line-height: 60px;
    }
  }
}
</style>