<template>
  <div class="yun-music-header">
    <div
      class="mask"
      :style="{
        backgroundImage:
          'url(' +
          details.coverImgUrl +
          '?imageView=1&type=webp&thumbnail=247x0)',
      }"
    ></div>
    <span class="iconfont icon-fanhui" @click="clickHandler"></span>
    <!-- <div
      class="mask"
      :style="{
        backgroundImage:
          'url(' +
          details.coverImgUrl +
          '?imageView=1&type=webp&thumbnail=247x0)',
      }"
    ></div> -->
    <div class="left">
      <span class="count">{{ playCount }}</span>
      <span class="gedan-icon"></span>
      <img :src="details.coverImgUrl | imageView(123)" alt="" />
    </div>
    <div class="right">
      <h4>{{ details.name }}</h4>
      <div class="icon_box">
        <img :src="details.creator.avatarUrl | imageView(50)" alt="" />
        <span class="circle"></span>
        <span class="icon">{{ details.creator.nickname }}</span>
        <b class="gt">&gt;</b>
      </div>
      <p class="time">{{ updateDate(date) }}</p>
      <p>{{ details.description }}</p>
      
    </div>
  </div>
</template>

<script>
export default {
  props: ["details"],
  data: function () {
    return {
      date: new Date(),
    };
  },
  computed: {
    playCount: function () {
      return (this.details.playCount / 10000).toFixed(1) + "万";
    },
  },
  methods: {
    clickHandler: function () {
      // router.go(-1) 后退一步记录，等同于 history.back()
      this.$router.go(-1);
    },
    updateDate: function (value) {
      if (!value) return "";
      var d = new Date(value);
      return d.getMonth() + 1 + "月" + d.getDate() + "日更新";
    },
  },
};
</script>

<style lang="less" scoped>
@import "../css/iconfont.css";
.yun-music-header {
  // margin-top: 80px;
  display: flex;
  position: relative;
  overflow: hidden;
  padding: 15px;

  .icon-fanhui {
    position: absolute;
    left: 10px;
    top: 10px;
    font-size: 20px;
    color: #fff;
  }

  .mask {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0;
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
    z-index: -1;
    transform: scale(1.5);
    filter: blur(20px) brightness(1.3);

    &:after {
      content: "";
      background: rgba(0, 0, 0, 0.25);
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
    }
  }
  .left {
    width: 30%;
    position: relative;
    margin-top: 20px;

    .count {
      position: absolute;
      top: 20%;
      right: 5px;
      font-size: 10px;
      text-align: center;
      color: #fff;
      background: rgba(0, 0, 0, 0.1);
      border-radius: 10px;
      padding: 2px 3px;
    }

    // .gedan-icon{
    //   display:block;
    //   background: url(../image/icon.png) no-repeat 0 -243px;
    //   background-size: 100% 100% ;
    //   background-position: center;
    //   width: 54px;
    //   height: 24px;
    // }

    img {
      width: 100%;
      margin: 30% 0;
    }
  }
  .right {
    flex: 1;
    // background: blue;
    margin-left: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 10px 0;
    position: relative;

    h4 {
      color: #fff;
    }

    .icon_box {
      display: flex;
      align-items: center;
      padding-top: 10px;

      .circle {
        background: url(../assets/icn_v_36.png) no-repeat;
        background-size: 100% 100%;
        width: 12px;
        height: 12px;
        margin-top: 15px;
        margin-left: -5px;
      }

      .gt {
        font-family: "宋体";
        padding-left: 3px;
        padding-top: 1px;
        color: #fff;
      }
    }

    img {
      width: 20px;
      height: 20px;
      border-radius: 50%;
      background-size: 100% 100%;
    }

    .icon {
      color: #fff;
      font-size: 12px;
      padding-left: 7px;
    }

    p {
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      color: #fff;
      font-size: 13px;
      padding-top: 5px;
    }
  }
}
</style>