      margin: 30% 0;
       background-image:-webkit-linear-gradient(top,rgba(0,0,0,.2),rgba(0,0,0,0));background-image:linear-gradient(180deg,rgba(0,0,0,.2),rgba(0,0,0,0));
    }
  }
  .right {
    flex: 1;
    // background: blue;
