<template>
  <div class="search">
    <div class="top_box">
      <div class="top">
        <div class="search_box" v-if="defaultWords">
          <span class="mirror"></span>
          <input
            type="text"
            v-model="value"
            :placeholder="defaultWords.showKeyword"
            @input="inputing"
          />
          <div class="del" @click="clearVal" v-if="value !== ''">
            <span></span>
          </div>
        </div>
      </div>
    </div>
    <!-- 默认搜索关键词 -->
    <section class="default" v-if="value === '' && !showDetail">
      <Loading v-if="value !== '' && !showDetail"></Loading>
      <p>热门搜索</p>
      <ul class="show_box">
        <li v-for="(item, index) in searchwords" :key="index">
          <span @click="clickHandler" class="des"> {{ item.first }}</span>
        </li>
      </ul>
    </section>

    <section class="history-record" v-if="showHistory  && !showDetail">
      <h4>历史记录</h4>
      <ul class="list">
        <li v-for="(item, index) in historyWord" :key="index" class="list-item">
          <i class="icon"></i>
          <div class="content">
            <span>{{ item }}</span>
            <i class="del" @click="delVal"></i>
          </div>
        </li>
      </ul>
    </section>

    <!-- 根据用户输入的搜索结果 -->
    <section class="keywords" v-if="!showDetail && value !== ''">
      <h3 class="title">搜索"{{ value }}"</h3>
      <Loading v-if="!keywords.length"></Loading>
      <ul class="keywords-lists" v-if="keywords">
        <li v-for="item in keywords" :key="item.id" @click="inputValue">
          <span class="icon"></span>{{ item.keyword }}
        </li>
      </ul>
    </section>

    <ul v-if="showDetail">
      <SongDetail
        :songlists="songlists"
        :currentPlayMusic="currentPlayMusic"
        @update:current-play-music="$emit('update:current-play-music', $event)"
        :playing="playing"
        :currentPlayMusicId="currentPlayMusicId"
        :singers="singers"
      ></SongDetail>
    </ul>
  </div>
</template>

<script>
import _ from "lodash";
import SongDetail from "@/components/SongDetail.vue";
import Loading from "@/components/Loading.vue";
export default {
  props: ["currentPlayMusic", "currentPlayMusicId", "playing"],
  components: {
    SongDetail,
    Loading,
  },
  data: function () {
    return {
      searchwords: null,
      value: "",
      keywords: [],
      show: false,
      defaultWords: null,
      showDetail: false,
      songlists: null,
      singers: null,
      historyWord: [],
      showHistory: false,
    };
  },
  mounted: function () {
    if (JSON.parse(localStorage.getItem("historyWord"))) {
      this.historyWord = JSON.parse(localStorage.getItem("historyWord"));
    }
  },
  created: function () {
    // 热门搜索
    this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
      setTimeout(() => {
        this.searchwords = res.data.result.hots;
        // console.log(this.searchwords)
      }, 1000);
    });

    // 默认搜索关键词
    this.axios
      .get("https://apis.netstart.cn/music/search/default")
      .then((res) => {
        this.defaultWords = res.data.data;
        // console.log("defaultWords", this.defaultWords);
      });

    this.debouncedSearch = _.debounce(this.Search, 500);
  },
  methods: {
    Search: function () {
      this.axios
        .get(
          "https://apis.netstart.cn/music/search/suggest?keywords=" +
            this.value +
            "&type=mobile"
        )
        .then((res) => {
          setTimeout(() => {
            this.keywords = res.data.result.allMatch;
            // console.log(res)
          }, 3000);
        });
    },
    inputValue: function (event) {
      this.value = event.target.innerText;
      this.showDetail = true;
      this.axios
        .get("https://apis.netstart.cn/music/search?keywords=" + this.value)
        .then((res) => {
          this.songlists = res.data.result.songs;
        });
      if (this.value === "") {
        return false;
      } else {
        if (!this.historyWord.includes(this.value)) {
          this.historyWord.unshift(this.value);
          localStorage.setItem("historyWord", JSON.stringify(this.historyWord));
        } else {
          let newArr = this.historyWord.indexOf(this.value);
          this.historyWord.splice(newArr, 1);
          this.historyWord.unshift(this.value);
          localStorage.setItem("historyWord", JSON.stringify(this.historyWord));
        }
      }

      // 多重匹配
      this.axios
        .get(
          "https://apis.netstart.cn/music/search/multimatch?keywords=" +
            this.value
        )
        .then((res) => {
          this.singers = res.data.result;
          // console.log(this.singers);
        });
    },
    clickHandler: function (event) {
      this.value = event.target.innerText;
      this.showHistory = false;
      if (this.value === "") {
        return false;
      } else {
        if (!this.historyWord.includes(this.value)) {
          this.historyWord.unshift(this.value);
          localStorage.setItem("historyWord", JSON.stringify(this.historyWord));
        } else {
          let newArr = this.historyWord.indexOf(this.value);
          this.historyWord.splice(newArr, 1);
          this.historyWord.unshift(this.value);
          localStorage.setItem("historyWord", JSON.stringify(this.historyWord));
        }
      }
    },
    clearVal: function () {
      this.showDetail = false;
      this.value = "";
      this.showHistory = !this.showHistory;
    },
    delVal: function () {
      // localStorage.removeItem(this.value);
      this.historyWord.pop();
    },

    inputing: function () {
      this.showHistory = false;
      this.show = true;
    },
  },
  watch: {
    value: function (n) {
      n ? this.debouncedSearch(n) : (this.keywords = []);
    },
  },
};
</script>

<style lang="less" scoped>
.search {
  margin-top: 125px;

  .top_box {
    background: #fff;
    height: 50px;
    width: 100vw;

    .top {
      margin-top: 10px;
      // width:100%;
      border-bottom: 1px solid #ebecec;
      padding: 15px 10px;
      background: #fff;

      .search_box {
        padding: 0 35px;
        box-sizing: border-box;
        background: #ebecec;
        border-radius: 30px;
        position: relative;
        margin-bottom: 5px;
        display: flex;
        align-items: center;

        .mirror {
          position: absolute;
          left: 0;
          top: 0;
          margin-top: 7px;
          margin-left: 7px;
          display: block;
          width: 15px;
          height: 15px;
          background: url(../assets/srch_item_icn_glass.png) no-repeat;
          background-size: 100% 100%;
          background-position: center;
        }

        input {
          width: 100%;
          height: 30px;
          line-height: 18px;
          font-size: 14px;
          border-radius: 15px;
          border: none;
          outline: none;
          background: #ebecec;
        }

        .del {
          position: absolute;
          right: 0;
          top: 0;
          width: 30px;
          height: 30px;
          line-height: 28px;
          text-align: center;
          span {
            display: inline-block;
            background: url(data:image/svg+xml;base64,);
            width: 14px;
            height: 14px;
            vertical-align: middle;
          }
        }
      }
    }
  }

  .keywords {
    width: 100vw;

    .keywords-lists {
      // background: pink;

      li {
        padding: 17px;
        display: flex;
        align-items: center;
        border-top: 1px solid #eeeeee;
        border-bottom: 1px solid #eeeeee;

        span {
          padding: 5px;
        }
        .icon {
          background: url(../assets/srch_item_icn_glass.png) no-repeat;
          background-size: 100% 100%;
          background-position: center;
          width: 6px;
          height: 6px;
          display: block;
          margin-right: 7px;
        }
        .con {
          font-size: 15px;
        }
      }
    }
  }
}

p {
  font-size: 14px;
  margin-bottom: 10px;
}

.title {
  height: 50px;
  margin-left: 10px;
  padding-right: 10px;
  font-size: 15px;
  line-height: 50px;
  color: #507daf;
  font-weight: normal;
  margin-top: 10px;
}
.default {
  padding: 30px 20px;
  .show_box {
    margin: 10px 0 7px;

    li {
      display: inline-block;
      height: 32px;
      margin-right: 8px;
      margin-bottom: 8px;
      padding: 0 14px;
      font-size: 14px;
      line-height: 32px;
      color: #333;
      border-radius: 32px;
      border: 1px solid #d3d4da;
      .count {
        font-family: normal;
      }
      span {
        font-size: 16px;
      }

      &:nth-child(odd) {
        margin-left: 15px;
      }
    }

    a {
      text-decoration: none;
      color: #888;
    }
  }

  .keywords {
    background: #000;
  }
}
.history-record {
  display: block;
  h4 {
    padding: 0px 15px;
    font-weight: normal;
  }
  .list {
    .list-item {
      display: flex;
      -webkit-box-align: center;
      align-items: center;
      height: 45px;
      .icon {
        display: inline-block;
        vertical-align: middle;
        width: 15px;
        height: 15px;
        background-image: url(data:image/svg+xml;base64,);
        background-position: 0 0;
        background-size: contain;
        background-repeat: no-repeat;
        margin: 0px 10px;
      }
      .content {
        flex: 1;
        align-items: center;
        position: relative;
        display: flex;

        span {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          word-break: normal;
          flex: 1;
          border-bottom: 1px solid #ebecec;
          color: gray;
        }

        .del {
          display: inline-block;
          width: 12px;
          height: 12px;
          background-image: url(data:image/svg+xml;base64,);
          vertical-align: middle;
          background-position: 0 0;
          background-size: contain;
          background-repeat: no-repeat;
          margin-left: 2px;
          margin-right: 10px;
        }
      }
    }
  }
}
</style>