<template>
  <div class="rank-songs" v-if="details">
    <RecommendDetail  :details="details" />
    <h3 class="title">歌曲列表</h3>
    <ul class="songs-lists">
      <LatestMusic
        :song="song"
        v-for="(song, index) in details.tracks.slice(0, 20)"
        :key="index"
        :order="index + 1"
        @update:current-play-music="$emit('update:current-play-music', $event)"
        :currentPlayMusicId="currentPlayMusicId"
        :playing="playing"
        :currentPlayMusic="currentPlayMusic"
        @click.native="$emit('update:playlist', details.tracks.slice(0, 20))"
      />
    </ul>
    <div class="more">查看更多歌曲，请下载客户端</div>
    <CommentList :comments="comments"></CommentList>
    <PlayMask> </PlayMask>
  </div>
</template>

<script>
import RecommendDetail from "@/components/RecommendDetail.vue";
import CommentList from "@/components/CommentList.vue";
import LatestMusic from "@/components/LatestMusic.vue";
import PlayMask from "@/components/PlayMask.vue";
export default {
  components: {
    RecommendDetail,
    LatestMusic,
    PlayMask,
    CommentList,
  },
  props: ["currentPlayMusicId", "playing", "currentPlayMusic"],
  data: function () {
    return {
      details: null,
      comments:null
    };
  },
  created: function () {
    this.getTracksById(this.$route.query.id);
    
    this.axios.get("https://apis.netstart.cn/music/comment/playlist?id="+this.$route.query.id + "&type=2").then(res => {
      // console.log(res);
      this.comments = res.data;
    })
  },
  methods: {
    getTracksById: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/playlist/detail?id=" + id)
        .then((res) => {
          this.details = res.data.playlist;
          // console.log(this.details);
        });
    },
  },
};
</script>

<style lang="less" scoped>
.rank-songs {
  .title {
    height: 23px;
    font-weight: normal;
    line-height: 23px;
    padding: 0 10px;
    font-size: 12px;
    color: #666;
    background-color: #eeeff0;
  }

  .more {
    margin-top: 13px;
    color: #999;
    font-size: 14px;
    text-align: center;
  }

 
}
</style>