<template>
  <div class="rank">
    <div class="container">
      <h3 class="title">榜单推荐</h3>
      <ul class="lists">
      <!-- <Loading v-if="!rankcategorylists.length"></Loading> -->
        <RankList
          :item="item"
          v-for="item in rankcategorylists.slice(23,26)"
          :key="item.id"
          @click.native="$router.push('/recommenddetail?id=' + item.id)"
          :currentPlayMusic="currentPlayMusic"
          :currentPlayMusicId="currentPlayMusicId"
        />
      </ul>
    </div>
    <div class="list_box">
      <div class="logo">
        <span class="icon"></span>
        <h3>官方版</h3>
      </div>
      <Loading v-if="!rankcategorylists.length"></Loading>
      <ul class="song-list">
        <RankCard
          :item="item"
          v-for="item in rankcategorylists.slice(0, 4)"
          :key="item.id"
          @click.native="$router.push('/ranksongs?id=' + item.id)"
          :currentPlayMusic="currentPlayMusic"
          :currentPlayMusicId="currentPlayMusicId"
        />
        <!-- .native修饰符用于把原生dom事件绑定到组件上面 -->
        <!-- router.push() 编程式导航-->
      </ul>
    </div>
    <transition name="fade">
      <PlayMask
        v-if="isFullScreen"
        :currentPlayMusic="currentPlayMusic"
        :playing="playing"
        :currentTime="currentTime"
        :duration="duration"
        @update:current-time="updateCurrentTime"
        @toggle-play="togglePlay"
        @toggle-playlist="showList = $event"
        @toggle-next="toggleNextMusic"
        @toggle-prev="togglePrevMusic"
        @toggle-playmode="togglePlayMode"
        :mode="mode"
      ></PlayMask>
    </transition>
  </div>
</template>

<script>
import RankList from "@/components/RankList.vue";
import RankCard from "@/components/RankCard.vue";
import PlayMask from "@/components/PlayMask.vue";
import Loading from "@/components/Loading.vue";
export default {
  props: ["currentPlayMusic", "currentPlayMusicId"],
  components: {
    RankList,
    PlayMask,
    RankCard,
    Loading,
  },
  data: function () {
    return {
      rankcategorylists: [],
      playlists: null,
      isFullScreen: false,
    };
  },
  created: function () {
    this.axios.get("https://apis.netstart.cn/music/toplist").then((res) => {
      setTimeout(() => {
        this.rankcategorylists = res.data.list;
        // console.log(this.rankcategorylists);
      }, 3000);
    });

    // this.axios
    //   .get("https://apis.netstart.cn/music/personalized/newsong")
    //   .then((res) => {
    //     // console.log(res.data.result);
    //   });
  },
};
</script>

<style lang="less" scoped>
.rank {
  .container {
    height: 190px;
    background: #f5f5f5;
    border-radius: 10px;
    margin: 15px;
    margin-top: 150px;

    .title {
      padding: 15px 10px;
    }

    .lists {
      // width: 100%;
      display: flex;
      list-style: none;
      border-radius: 10px;
    }
  }
}
.list_box {
  .logo {
    display: flex;
    align-items: center;
    margin-bottom: -10px;

    .icon {
      display: inline-block;
      background: url(../image/topbar.png) no-repeat 0 -19px;
      width: 33px;
      height: 33px;
      margin: 15px;
    }

    h3 {
      display: inline-block;
      margin-left: -5px;
    }
  }
}
</style>