<template>
  <div class="play" v-if="currentPlayMusic">
    <audio
      :src="currentPlaySongUrl"
      controls
      style="height: 50px"
      v-if="currentPlayMusic"
      autoplay
      ref="audio"
      @play="onPlay"
      @pause="onPause"
      @timeupdate="onCurrentTime"
      @durationchange="onDurationChange"
      @ended="toggleNextMusic"
      @started="togglePrevMusic"
    ></audio>
    <PlayBar
      :currentPlayMusic="currentPlayMusic"
      :playing="playing"
      :currentPlayMusicId="currentPlayMusicId"
      @click.native="isFullScreen = true"
      :currentTime="currentTime"
      :duration="duration"
      @toggle-playlist="showList = $event"
    />
    <!-- 给PlayMask组件添加过渡效果（淡入淡出） -->
    <transition name="fade">
      <PlayMask
        v-if="isFullScreen"
        :currentPlayMusic="currentPlayMusic"
        :currentPlayMusicId="currentPlayMusicId"
        :playing="playing"
        :currentTime="currentTime"
        :duration="duration"
        @update:current-time="updateCurrentTime"
        @toggle-play="togglePlay"
        @toggle-playlist="showList = $event"
        @toggle-next="toggleNextMusic"
        @toggle-prev="togglePrevMusic"
        @toggle-playmode="togglePlayMode"
        @click="$emit('update:current-play-music',$event)"
        :mode="mode"
      ></PlayMask>
    </transition>

    <!-- 需要频繁切换的时候使用v-show -->
    <section class="list" @click="showList = false" v-show="showList">
      <transition name="fade">
        <div class="mask_layer" v-if="showList"></div>
      </transition>
      <transition name="slide">
        <div class="content" v-if="showList">
          <ul>
            <LatestMusic
            v-for="song in playlist"
            :key="song.id"
            :song="song"
              @update:current-play-music="
                $emit('update:current-play-music', $event)
              "
              :playing="playing"
              :currentPlayMusic="currentPlayMusic"
              :currentPlayMusicId="currentPlayMusicId"
              :playlist="playlist"
            ></LatestMusic>
          </ul>
        </div>
      </transition>
    </section>
  </div>
</template>

<script>
import PlayBar from "@/components/PlayBar.vue";
import PlayMask from "@/components/PlayMask.vue";
import LatestMusic from "@/components/LatestMusic.vue";
// import ShowGedanList from "@/components/ShowGedanList.vue";
export default {
  components: {
    PlayBar,
    PlayMask,
    LatestMusic,
    // ShowGedanList,
  },
  props: ["currentPlayMusicId", "currentPlayMusic", "playing", "playlist"],
  data: function () {
    return {
      // playing:false,
      isFullScreen: false,
      show: true,
      currentTime: 0,
      duration: 0,
      showList: false,
      mode: "order"
    };
  },
  // 计算当前播放歌曲的url
  computed: {
    currentPlaySongUrl: function () {
      if (!this.currentPlayMusic) return "";
      return `https://music.163.com/song/media/outer/url?id=${this.currentPlayMusic.id}.mp3`;
    },
  },
  methods: {
    onPlay: function () {
      // 不是自己的状态不能直接改
      // this.playing = true;
      // 使用自定义事件$emit()
      this.$emit("update:playing", true);
    },
    onPause: function () {
      // 不是自己的状态不能直接改
      // this.playing = false;
      this.$emit("update:playing", false);
    },
    onCurrentTime: function (event) {
      // console.log(this.$refs.audio.currentTime);
      // this.currentTime = this.$refs.audio.currentTime;
      this.currentTime = event.target.currentTime;
      // console.log(this.currentTime / 60 );
    },
    onDurationChange: function (event) {
      // console.log(this.$refs.audio.duration);
      // this.duration = this.$refs.audio.duration;
      this.duration = event.target.duration;
    },
    updateCurrentTime: function (time) {
      this.$refs.audio.currentTime = time;
    },
    togglePlay: function (playing) {
      playing ? this.$refs.audio.play() : this.$refs.audio.pause();
    },
    toggleNextMusic: function () {
      var nextMusic;
      var currentMusicIndex;
      // this.mode = ( this.mode + 1) % 3
      switch (this.mode) {
        case "order":
          currentMusicIndex = this.playlist.findIndex(
            (item) => item.id === this.currentPlayMusicId
          );
          var nextIndex =
            currentMusicIndex === this.playlist.length - 1
              ? 0
              : currentMusicIndex + 1;
          nextMusic = this.playlist[nextIndex];
          break;
        case "random":
          var leftMusic = this.playlist.filter(
            (item) => item.id !== this.currentPlayMusicId
          );
          nextMusic = leftMusic[Math.floor(Math.random() * leftMusic.length)];
          break;
          case "single":
          // this.$refs.audio.currentTime = 0;
          nextMusic = this.playing[currentMusicIndex]
          break;
        default:
      }
      this.$emit("update:current-play-music", nextMusic);
    },
    togglePrevMusic: function () {
      var prevMusic;
      var currentMusicIndex;
      currentMusicIndex = this.playlist.findIndex(
        (item) => item.id === this.currentPlayMusicId
      );
      var prevIndex =
        currentMusicIndex === -1
          ? this.playlist.length - 1
          : currentMusicIndex - 1;
      prevMusic = this.playlist[prevIndex];
      this.$emit("update:current-play-music", prevMusic);
    },
    togglePlayMode: function () {
        if(this.mode === "random"){
          this.mode = "order"
        }else{
          this.mode = "random";
        }
    },
   
  },
};
</script>

<style lang="less" scoped>
.play {
  audio {
    margin-bottom: 50px;
    display: none;
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: opacity 0.5s;
  }
  .fade-enter,
  .fade-leave-to {
    opacity: 0;
  }

  .slide-enter-active,
  .slide-leave-active {
    transition: all 0.5s;
  }
  .slide-enter,
  .slide-leave-to {
    transform: translateY(60vh);
  }
  .slide-enter-to,
  .slide-leave {
    transform: translateY(0px);
  }

  .list {
    width: 100vw;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 101;

    .mask_layer {
      width: 100vw;
      height: 100vh;
      position: absolute;
      left: 0;
      top: 0;
      background: rgba(0, 0, 0, 0.5);
    }

    .content {
      position: absolute;
      left: 0;
      bottom: 0;
      width: calc(100% - 30px);
      height: 60vh;
      background: #f5f5f5;
      // background:pink;
      margin: 15px;
      border-radius: 15px;
      z-index: 101;
      overflow: auto;
    }
  }
}
</style>