<template>
  <div class="play-mask" v-if="currentPlayMusic" :class="{ playing: playing }">
    <div
      class="mask_layer"
      :style="{
        backgroundImage:
          'url(' + (currentPlayMusic.picUrl ||
          currentPlayMusic.al.picUrl ) + ')',
      }"
    ></div>
    <!-- 头部 -->
    <div class="top">
      <div class="close" @click="$parent.isFullScreen = false">
        <span class="iconfont icon-xiala"></span>
      </div>
      <div class="title">
        <h4>{{ currentPlayMusic.name }}</h4>
        <div class="them">
          <p
            v-for="(item, index) in currentPlayMusic.song
              ? currentPlayMusic.song.artists
              : currentPlayMusic.ar"
            :key="index"
          >
            {{ item.name }}
          </p>
        </div>
      </div>
      <div>
        <span class="iconfont icon-fenxiang1"></span>
      </div>
    </div>
    <section
      style="position: relative; width: 100vw; height: calc(100% - 150px)"
    >
      <!-- <keep-alive></keep-alive>主要用于保留组件或避免重新渲染 -->
      <!-- <KeepAlive> -->
        <!-- 唱片组件 -->
        <PlayDisc
          v-if="showDisc"
          @click.native="showDisc = false"
          :currentPlayMusic="currentPlayMusic"
          :playing="playing"
          :class="{ playing: playing }"
        />
        <!-- 歌词组件 -->
        <PlayLyric
          v-else
          @click.native="showDisc = true"
          :currentPlayMusicId="currentPlayMusic.id"
          :currentTime="currentTime"
          :playing="playing"
        />
      <!-- </KeepAlive> -->
    </section>
    <div class="footer">
      <div class="progress">
        <input
          type="range"
          min="0"
          :max="duration"
          v-model="value"
          @change="changeHandler"
          @input="inputing = true"
        />
        <b class="curr-time">{{ formatSeconds(currentTime) }}</b>
        <!-- 进度条 -->
        <div class="bar">
          <span :style="{ width: (value / duration) * 100 + '%' }"></span>
        </div>
        <b class="duration">{{ formatSeconds(duration) }}</b>
      </div>
      <!-- 底部控件 -->
      <div class="controler">
        <span
          @click="$emit('toggle-playmode')"
          :class="{ current: mode === 'random' }"
        ></span>
        <span @click="$emit('toggle-prev', currentPlayMusic.id)"></span>
        <span @click="$emit('toggle-play', !playing)"></span>
        <!-- <span @click="$emit('toggle-next',currentPlayMusic.id)"></span> -->
        <span @click="$parent.toggleNextMusic()"></span>
        <span @click="$emit('toggle-playlist', true)"></span>
      </div>
    </div>
  </div>
</template>

<script>
import PlayDisc from "@/components/PlayDisc.vue";
import PlayLyric from "@/components/PlayLyric.vue";

export default {
  components: {
    PlayDisc,
    PlayLyric,
  },
  props: ["currentPlayMusic", "playing", "currentTime", "duration", "mode","currentPlayMusicId"],
  data: function () {
    return {
      // isFullScreen:false
      inputing: false,
      value: this.currentTime,
      showDisc: true,
    };
  },
  methods: {
    changeHandler: function () {
      this.inputing = false;
      this.$emit("update:current-time", this.value);
    },
    formatSeconds: function (time) {
      let secondTime = parseInt(time);
      let min = 0;
      let result = "";
      if (secondTime > 60) {
        min = parseInt(secondTime / 60);
        secondTime = parseInt(secondTime % 60);
        if (min > 60) {
          min = parseInt(min % 60);
        }
      }
      // padStart填充指定位数的字符串
      result = `${min
        .toString()
        .padStart(2, "0")}:${secondTime.toString().padStart(2, "0")}`;
      return result;
    },
  },
  watch: {
    currentTime: function (n) {
      if (!this.inputing) {
        this.value = n;
      }
    },
  },
};
</script>

<style lang="less" scoped>
@import "../css/iconfont.css";
* {
  margin: 0;
  padding: 0px;
}
.play-mask {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.08);
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 101;
  // padding:10px;
  overflow: hidden;

  .mask_layer {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
    overflow: hidden;
    z-index: -1;
    transform: scale(1.5);
    filter: brightness(0.8) blur(20px);
  }

  .top {
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 111;
    position: relative;
    margin-bottom: 10px;

    .close {
      span {
        position: absolute;
        left: 10px;
        top: 10px;
        text-align: center;
        font-size: 24px;
        font-weight: bold;
        color: #fff;
      }
    }

    .title {
      display: flex;
      align-items: center;
      flex-direction: column;
      margin-top: 10px;

      h4 {
        color: #fff;
        font-weight: normal;
        font-size: 18px;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }

      .them {
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
        p {
          color: hsla(0, 0%, 100%, 0.6);
          font-size: 16px;
          white-space: nowrap;

          // &::after {
          //   content: "/";
          // }

          &:last-child {
            content: "";
          }
        }
      }
    }
    .icon-fenxiang1 {
      font-size: 24px;
      color: #fff;
      position: absolute;
      right: 10px;
      top: 10px;
    }
  }

  .footer {
    height: 100px;
    width: 100%;
    position: absolute;
    left: 0;
    // bottom: 100px;
    bottom: 0;

    .progress {
      position: relative;
      // margin: 10px 50px;
      padding:10px 40px;
      input {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        opacity: 0;
      }

      .curr-time {
        position: absolute;
        left: 3px;
        top: 4px;
        color: hsla(0, 0%, 100%, 0.6);
        font-size: 12px;
      }
    }

    .bar {
      width: 100%;
      height: 2px;
      background: rgba(255, 255, 255, 0.2);
      // position: absolute;
      // left: 13px;
      // bottom: -20px;

      span {
        display: block;
        align-items: center;
        width: 0%;
        height: 100%;
        background: rgba(255, 255, 255, 0.2);
        position: relative;

        &::after {
          content: "";
          display: block;
          width: 6px;
          height: 6px;
          position: absolute;
          right: -3px;
          bottom: -2px;
          border-radius: 50%;
          background: #fff;
        }
      }
    }

    .duration {
      position: absolute;
      right: 4px;
      top: 4px;
      color: hsla(0, 0%, 100%, 0.6);
      font-weight: normal;
      font-size: 12px;
    }
  .controler {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 10px;
    span {
      width: 40px;
      height: 40px;
      // background: green;
      display: block;
      margin: 10px;
      background-image: url(../assets/pause.png);
      background-size: 100%;
      background-position: center;

      &:nth-child(1) {
        background-image: url(../assets/loop.png);

        &.current{
          background: url(../assets/sj.png) no-repeat;
          background-size: 100% 100%;
        }
      }
      &:nth-child(2) {
        background-image: url(../assets/prev.png);
      }
      &:nth-child(3) {
        width: 50px;
        height: 50px;
      }
      &:nth-child(4) {
        background-image: url(../assets/next.png);
      }
      &:nth-child(5) {
        background-image: url(../assets/list.png);
      }
    }
    
    
  }

  }


}
// 播放暂停图标切换
&.playing {
  .controler {
    span:nth-child(3) {
      background: url(../assets/play.png) no-repeat;
      background-size: 100% 100%;
    }
  }
}
</style>