<script>
// @ is an alias to /src
import HomeTitle from "@/components/HomeTitle.vue";
import RecommendList from "@/components/RecommendList.vue";
import LatestMusic from "@/components/LatestMusic.vue";
import Loading from "@/components/Loading.vue";

export default {
  name: "Home",
  props: ["currentPlayMusicId","playing","currentPlayMusic"],
  components: {
    HomeTitle,
    RecommendList,
    LatestMusic,
    Loading
  },
  // data是一个函数，返回一个对象
  data: function () {
    return {
      personalizeds: [],
      songlists: [],
    };
  },
  created: function () {
    // 编辑推荐
    this.axios
      .get("https://apis.netstart.cn/music/personalized?limit=6")
      .then((res) => {
        setTimeout(() => {
          this.personalizeds = res.data.result;
          // console.log(this.personalizeds);
        },3000)
      });

    // 最新音乐
    this.axios
      .get("https://apis.netstart.cn/music/personalized/newsong")
      .then((res) => {
        setTimeout(() => {
           this.songlists = res.data.result;
        // console.log(this.songlists);
        },3000)
       
      });
  },
};
</script>

