<template>
  <div class="play-lyric" ref="lyricEl" :class="{ playing: playing }" v-if="parsedLyric">
    <!-- ref访问子组件实例或子元素 -->
    <section class="con">
      <ul
        ref="lisEl"
        :style="{
          marginTop: `-${frontLisHeight}px`,
          transform: `translateY(${lyricElHeight / 2}px)`,
        }"
      >
        <li
          v-for="(item, index) in parsedLyric"
          :key="index"
          :class="{ active: currentLyricIndex === index }"
        >
          <span
            :style="{
              animationDuration: `${
                index === parsedLyric.length - 1
                  ? 5
                  : parsedLyric[index + 1].time - item.time
              }s`,
            }"
          >
            {{ item.text || "-------" }}</span
          >
        </li>
      </ul>
    </section>
  </div>
</template>

<script>
export default {
  props: ["currentPlayMusicId", "currentTime", "playing"],
  data: function () {
    return {
      lyric: "",
      frontLisHeight: 0,
      lyricElHeight: 0,
    };
  },
  created: function () {
    this.axios
      .get("https://apis.netstart.cn/music/lyric?id=" + this.currentPlayMusicId)
      .then((res) => {
        this.lyric = res.data.lrc.lyric;
      });
  },

  mounted: function () {
    this.lyricElHeight = this.$refs.lyricEl.offsetHeight;
  },
  //   g 全局匹配
  // i 大小写不敏感
  // m 多行匹配
  // \d 查找数字
  // match 找到一个或多个正则表达式的匹配
  // ？匹配0次或1次
  // （） 匹配一个子表达式的开始和结束位置
  computed: {
    // 原始
    parsedLyric: function () {
      return this.lyric
        .split("\n")
        .filter((s) => s)
        .map((item) => {
          var match = item.match(
            /^\[(?<time>\d{2}:\d{2}\.\d{2,3})\] *(?<text>.*)$/im
          );

          return match.groups;
        })
        .map((o) => {
          var match = o.time.match(/(?<m>\d{2}):(?<s>\d{2}\.\d{2,3})/i);
          //   console.log(match.groups.m * 60 + match.groups.s * 1);
          return {
            ...o,
            time: (match.groups.m * 60 + match.groups.s * 1).toFixed(3),
          };
        });
    },
    // 当前歌词索引
    currentLyricIndex: function () {
      var index = this.parsedLyric.findIndex(
        (item) => item.time > this.currentTime
      );
      return index === -1 ? this.lyric.length - 1 : index - 1;
    },
  },
  watch: {
    currentLyricIndex: function (n) {
      // var frontLis = this.$refs.lisEl.querySelectorAll("li"); // 类数组（伪数组），没有slice()方法
      // console.log(frontLis,n);
      // 使用扩展运算符...把类数组转成真正的数组，使用slice()方法截取
      var frontLis = [...this.$refs.lisEl.querySelectorAll("li")].slice(0, n);
      // console.log(frontLis);
      // 计算当前高亮行前面的所有li的高度
      // reduce()方法 计算数组元素相加后的总和
      let frontLisHeight = frontLis.reduce((total, lisEl) => {
        return total + lisEl.offsetHeight;
      }, 0);
      // console.log(frontLisHeight);
      this.frontLisHeight = frontLisHeight;
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes gradient {
  from {
    background-position-x: 100%;
  }
  to {
    background-position-x: 0%;
  }
}
.play-lyric {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  // background: cyan;
  overflow: hidden;

  .con {
    overflow: hidden;
    flex: 1;
    mask-image: linear-gradient(
      to bottom,
      transparent 0% 5%,
      red 15% 85%,
      transparent 95% 100%
    );

    ul {
      text-align: center;
      transition: all 0.3s;

      li {
        padding: 10px;
        span {
          color:hsla(255,255,255,.6);
        }
        &.active {
          span {
            // color: #fff;
            background-image: linear-gradient(
              to right,
               #d43c33 0% 48%,
               #fff 50% 100%,
            );
            background-size: 200%;
            background-position-x: 30%;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            animation: gradient 3s linear 0s;
            animation-play-state: paused;
          }
        }
      }
    }
  }
  &.playing {
    ul {
      li {
        &.active {
          span {
            animation-play-state: running;
          }
        }
      }
    }
  }
}
</style>