<template>
  <div class="rank-songs" v-if="details">
    <RankSongsHeader :details="details" />
    <ul class="songs-lists" style="margin-top: 40px">
      <LatestMusic
        :song="song"
        v-for="(song, index) in details.tracks.slice(0, 20)"
        :key="index"
        :order="index + 1"
        @update:current-play-music="$emit('update:current-play-music', $event)"
        :currentPlayMusicId="currentPlayMusicId"
        :currentPlayMusic="currentPlayMusic"
        :playing="playing"
        @click.native="$emit('update:playlist', details.tracks.slice(0, 20))"
      />
    </ul>
    <PlayMask></PlayMask>
  </div>
</template>

<script>
import RankSongsHeader from "@/components/RankSongsHeader.vue";
import LatestMusic from "@/components/LatestMusic.vue";
import PlayMask from "@/components/PlayMask.vue";

export default {
  components: {
    RankSongsHeader,
    LatestMusic,
    PlayMask,
  },
  props: ["playing", "currentPlayMusicId", "currentPlayMusic"],
  data: function () {
    return {
      details: null,
      records: null,
    };
  },
  created: function () {
    this.getTracksById(this.$route.query.id);
  },
  methods: {
    getTracksById: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/playlist/detail?id=" + id)
        .then((res) => {
          this.details = res.data.playlist;
          // console.log(this.details);
        });
    },
  },
};
</script>

